.banner {
    height: 80vh;
    background: url("https://source.unsplash.com/1920x1080/?nature,landscape") no-repeat center center/cover;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    text-align: center;
    position: relative;
    overflow: hidden;
  }
  .banner::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5); 
    background: url(https://kia.car.com/assets/sliderbanners/bg-black-friday.jpg) center bottom / cover no-repeat;
    z-index: 1;
  }
  .banner:before {
    content: "";
    display: block;
    background: url(https://kia.car.com/assets/sliderbanners/bg-black-friday.jpg) center bottom / cover no-repeat;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
  }
  .banner-content {
    position: relative;
    z-index: 2;
  }
  .banner h1 {
    font-size: 3.5rem;
    margin-bottom: 1rem;
    color: white;
    font-weight: 400;
  }
  .banner p {
    font-size: 2rem;
    margin-bottom: 2rem;
    color: white;
  }
  .btnExplore {
    max-width: 21.5rem;
    color: black;
    font-size: 1.8rem;
    font-weight: 700;
    background-color: white;
    display: inline-block;
    padding: 0.8rem 2.5rem;
    border-radius: 50px;
    text-decoration: none;
    transition: background 0.3s ease;
  }
  .btnExplore:hover {
    background:#b70003;
    color:whitesmoke;
  }
  .carImg{
    z-index: 2;
  }
  @media (max-width: 768px) {
    .banner h1 {font-size: 2rem;}
    .banner p {font-size: 1rem;}
    .btn {font-size: 0.9rem;padding: 0.6rem 1.2rem;}
  }
  
